html{
  overflow-x: hidden;
}
*{
  margin:0;
  padding:0;
}
.App {
  text-align: center;
  font-family: 'Open Sans', sans-serif;
}
body{
  background-color: #f6faff;
}
.navbar{
  height: 6vh;
  width: 100vw;
  background-color: white;
  display: flex;
  justify-content: space-between;
  font-family: 'Open Sans', sans-serif;
  font-size: 1.1em;
  font-weight: 350;
  box-shadow: 0px 2px 3px rgba(0, 0, 0, .5);
  position: absolute;
  top:0;
  z-index: 1;
}
.navbar-sign-in-out{
  width: 15%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 3%;
}
.navbar-sign-in-out a{
  text-decoration: none;
  color:black;
  border:1px solid rgba(0, 0, 0, .5);
  padding:5px 25px;
  font-weight: 550;
  transition: all 200ms;
}
.navbar-sign-in-out a:hover{
  background-color: rgb(0, 0, 0);
  color:white;
}
.username-profilebtn-wrapper{
  position: relative;
}
.view-profile-btn{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  background: none;
  border:none;
  font-size: 1.1em;
}
.caret{
  width: 10px;
  margin-left: 10px;
}
.view-profile-div{
  width: 100%;
  height: 0px;
  background-color: black;
  position: absolute;
  left: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  transition: all 150ms;
}
.profile-div-active{
  height: 30px;
  border:1px solid black;
}
.view-profile-div a{
  font-size: .9em;
  color:white;
  padding:0;
  border:0;
}
.navbar-btns{
  height: 100%;
  width: 10%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 2%;
}
.navbar-btns a{
  color:black;
  text-decoration: none;
}
.hamburger-menu{
  display: none;
}
.sidebar{
  display: none;
}
.sidebar-btn{
  display: none;
}

@media(max-width:1280px){
  html{
    max-width: 100vw;
    overflow-x: hidden;
  }
  .navbar{
    width: 100vw;
    height: 8vh;
  }
  .navbar-btns{
    width: 15%;
  }
  .navbar-sign-in-out{
    width: 30%;
  }
  .username-profilebtn-wrapper{
    min-width: 50%;
  }
  .navbar-sign-in-out a{
    padding:5px 5px;
  }
}
@media(max-width:1024px){
