/*Loading Animation*/
.loader-wrapper{
  margin:0;
  padding:0;
  height: 100vh;
  width: 100vw;
  position:absolute;
  top:0;
  left:0;
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, .8);
}
.lds-ellipsis {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ellipsis div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #fff;
  -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
          animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.lds-ellipsis div:nth-child(1) {
  left: 8px;
  -webkit-animation: lds-ellipsis1 0.6s infinite;
          animation: lds-ellipsis1 0.6s infinite;
}
.lds-ellipsis div:nth-child(2) {
  left: 8px;
  -webkit-animation: lds-ellipsis2 0.6s infinite;
          animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(3) {
  left: 32px;
  -webkit-animation: lds-ellipsis2 0.6s infinite;
          animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(4) {
  left: 56px;
  -webkit-animation: lds-ellipsis3 0.6s infinite;
          animation: lds-ellipsis3 0.6s infinite;
}
@-webkit-keyframes lds-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes lds-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@-webkit-keyframes lds-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}
@keyframes lds-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}
@-webkit-keyframes lds-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(24px, 0);
            transform: translate(24px, 0);
  }
}
@keyframes lds-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(24px, 0);
            transform: translate(24px, 0);
  }
}
/*Page Style*/
.user-profile-page{
    height: 90vh;
    width: 95vw;
    border-radius: 8px;
    margin:auto;
    margin-top: 8vh;
    box-shadow: 2px 2px 5px black;
    background-color: white;
    display: flex;
    align-items: center;
    font-family: "Open Sans", sans-serif;
  }
  .user-profile-sidebar{
    width: 15%;
    height: 90%;
    border-right: 1px solid black;
  }
  .my-profile-h2{
    text-align: center;
    margin:10% 0;
  }
  .user-icon-wrapper, .my-posts-wrapper{
    height: 6%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5em;
    padding: 15px 0;
    cursor: pointer;
    transition: all 200ms;
    border-top:1px solid black;
    border-bottom: 1px solid black;
    margin-top: 5%;
  }
  .user-icon-wrapper:hover, .my-posts-wrapper:hover{
    background-color: #ebebeb;
  }
  .my-posts-wrapper a{
    text-decoration: none;
    color:black;
  }
  .user-icon-wrapper label, .my-posts-wrapper label{
    cursor: pointer;
  }
  .user-icon, .posts-icon{
    width: 25px;
    height: 22px;
    margin-right: 10px;
  }
  .posts-icon, .my-posts-wrapper label{
    vertical-align: middle;
    display: inline-block;
  }
  .user-profile-data{
    height: 90%;
    width: 80%;
    margin:auto;
  }
  .user-profile-data h1{
    text-align: center;
    padding: 2% 0;
    border-bottom: 1px solid black;
  }
  .user-info{
    height: 50%;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-top:5%;
  }
  .full-name-wrapper, .email-wrapper, .password-wrapper{
    position: relative;
    height: 17%;
  }
  .user-info input:not(.profile-picture-input){
    font-size: 1.2em;
    padding:10px 15px;
    border: 1px solid black;
    border-radius: 3px;
    box-shadow: 1px 1px 2px black;
  }
  .profile-picture-wrapper{
    height: 30%;
    width: 35%;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  .profile-picture-input::-webkit-file-upload-button{
    font-size: 1.5em;
    padding:5px 10px;
    background: none;
    border:1px solid black;
    border-radius: 3px;
    cursor: pointer;
    -webkit-transition: all 200ms;
    transition: all 200ms;
  }
  .profile-picture-input::-webkit-file-upload-button:hover{
    background-color: black;
    color:white;
  }
  .profile-picture{
    width: 80px;
  }
  .profile-picture-inputs{
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* background-color: red; */
  
  }
  .uploadBtn{
    font-size: 1.2em;
    padding:5px 10px;
    background: none;
    border:1px solid black;
    border-radius: 3px;
    cursor: pointer;
    transition: all 200ms;
  }
  .uploadBtn:hover{
    background-color: black;
    color:white;
  }
  .uploadBtndisabled{
    font-size: 1.2em;
    padding:5px 10px;
    background: none;
    border:1px solid black;
    border-radius: 3px;
    cursor: not-allowed;
  }
 
  .edit-btn{
    position: absolute;
    bottom:0;
    right: 0;
    background: none;
    border:none;
    color:rgba(0, 150, 150);
    cursor: pointer;
  }
  .edit-btn:hover{
    text-decoration: underline;
  }
  .save-btn{
    position: absolute;
    left: 0;
    bottom:0;
    background: none;
    border:none;
    color:rgba(0, 150, 150);
    cursor: pointer;
    display: none;
  }
  .save-btn:hover{
    text-decoration: underline;
  }

@media(max-width:1280px){
  .user-profile-page{
    margin-left: 2%;
    margin-top:9vh;
  }
  .edit-btn{
    bottom: -6px;
  }
}
@media(max-width:1024px){
  .navbar-sign-in-out{
    width: 30%;
  }
  .user-info{
    font-size: .9em;
  }
  .edit-btn, .save-btn{
    bottom: -20px;
  }
  .profile-picture-wrapper{
    height: 40%;
    margin-top: -5%;
    margin-left: 3%;
  }
}
@media(max-width:912px){
  .user-profile-sidebar{
    width: 30%;
    height: 100%;
  }
  .user-info{
    flex-direction: column;
    align-items: center;
    font-size: 1.5em;
  }
  .edit-btn, .save-btn{
    top:55px;
    font-size: 1.3em;
  }
  .profile-picture-wrapper{
    width: 80%;
    margin-bottom:-20%;
    margin-left:5%;
    font-size: 1.5em;
  }
}
@media(max-width:820px){
  .navbar-sign-in-out a{
    font-size: 1em;
  }
}
@media(max-width:768px){
  .edit-btn, .save-btn{
    font-size: .9em;
  }
}
@media(max-width:540px){
  .user-profile-data h1{
    font-size: 1.5em;
  }
  .user-profile-sidebar h1{
    font-size: 1.5em;
    margin-top:50%;
  }
  .user-info{
    height: 40%;
    font-size: 1em;
  }
  .edit-btn, .save-btn{
    font-size: .9em;
  }
  .profile-picture-wrapper{
    font-size: .9em;
    width: 95%;
    margin-top:15%
  }
  .profile-picture-input::-webkit-file-upload-button{
    font-size: 1em;
  }
  .profile-picture{
    width: 60px;
  }
}
@media(max-width:414px){
  .user-profile-sidebar{
    display: none;
  }
  .profile-picture-wrapper{
    width: 110%;
    margin-left: 0;
  }
}
@media(max-width:360px){
  .profile-picture-wrapper p{
    margin-left: 5%;
  }
  .profile-picture-input{
    margin-left: 5%;
  }
  .profile-picture-wrapper button{
    width: 80%;
    margin-left: 5%;
  }
}
.page-not-found{
    width: 100vw;
    height: 30vh;
    font-family: 'Open Sans', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
  }
  .page-not-found a{
    color:#6098fb
  }
.user-posts-container{
    width: 50%;
    margin:auto;
    margin-top:8vh;
    font-family: 'Open Sans', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.user-posts-container h1{
    margin-bottom: 5%;
}
.post{
    width: 80%;
    margin-bottom:5%;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items:center;
    position:relative;
    border: 1px solid rgba(0, 0, 0, .5);
    border-radius: 5px;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, .5);
  }
  .author-details-wrapper{
    width: 100%;
    padding:1% 0;
    font-size: 1.3em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .author-name-profile-pic-wrapper{
    width: 30%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin:1% 0 1% 2%;
    overflow: hidden;
  }
  .post-pfp{
    height: 30px;
    border-radius: 50%;
  }
  .delete-btn-wrapper{
    width: 10%;
    display: flex;
    justify-content: center;
  }
  .delete-btn{
    height: 30px;
    cursor: pointer;
  }
  .title-wrapper{
    width: 100%;
    padding:2% 0;
    border-top:1px solid black;
    border-bottom:1px solid black;
    margin-bottom: 5%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .blog-wrapper{
    width: 90%;
    height: 90%;
    margin-bottom:5%;
    position: relative;
  }
  @media(max-width:1280px){
    .post{
      width: 50vw;
    }
    .author-details-wrapper{
      height: 6vh;
    }
    .author-name-profile-pic-wrapper{
      height: 100%;
      width: 50%;
    }
    .post-pfp{
      height: 40px;
    }
    .blog-wrapper{
      font-size: 1.6em;
    }
  }
  @media(max-width:1024px){
    .author-details-wrapper{
      height: 8vh;
    }
    .author-name-profile-pic-wrapper{
      width: 65%;
      justify-content: flex-start;
    }
  }
  @media(max-width:912px){
    .user-posts-container{
      width:100vw;
      margin:auto;
      margin-top: 10vh;
    }
    .post{
      width: 80vw;
      font-size: 1.3em;
    }
    .blog-wrapper{
      font-size: 1.2em;
    }
    .author-details-wrapper{
      height: 70px;
    }
    .author-name-profile-pic-wrapper{
      font-size: 1.2em;
      width: 90%;
    }
  }
  @media(max-width:540px){
    body{
      width: 100vw;
    }
    .user-posts-container{
      width: 100vw;
    }
    .author-name-profile-pic-wrapper{
      width: 85%;
      font-size: .7em;
    }
    .blog-wrapper{
      font-size: .8em;
    }
    .title-wrapper{
      padding:3% 0;
    }
    .edit-icon{
      right: -20px;
      bottom: -20px;
    }
  }
  @media(max-width:414px){
    .post{
      width: 95%;
      margin-left:1px;
    }
    .author-name-profile-pic-wrapper{
      width: 50%;
    }
    .post-pfp{
      height: 25px;
    }
    .blog-wrapper{
      font-size: 1em;
    }
    .blog-wrapper p{
        overflow-wrap: break-word;
    }
    .create-post-mobile{
      display: block;
      position: fixed;
      left:10px;
      bottom: 10px;
      box-shadow: 0px 0px 5px black;
    }
    .create-post-mobile{
      background: rgba(255, 255, 255, .5);
      border:none;
      border-radius: 50%;
    }
    .create-post-mobile img{
      width: 60px;
    }
    .edit-icon{
      right: -15px;
      bottom:-15px;
    }
  }
  @media(max-width:280px){
    .author-name-profile-pic-wrapper{
      width: 50%;
    }
  }
